
          @import "src/assets/styles/_vars.scss"; 
        
.weather-location-item {
  background-color: $grey;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  border-radius: 10px;
  align-items: center;
  margin-bottom: 10px;

  &:last-child {
    margin-bottom: 0;
  }

  &.enter {
    opacity: 0;
    transform: translateX(100%);
  }

  &.enter-active {
    opacity: 1;
    transform: translateX(0);
    transform-origin: right;
    transition: 0.3s;
  }

  &.exit {
    opacity: 1;
    transform: translateX(0);
  }

  &.exit-active {
    opacity: 0;
    transform: translateX(100%);
    transform-origin: right;
    transition: 0.3s;
  }

  .name {
    color: white;
    font-size: 24px;

    svg {
      margin-left: 10px;

      @media (max-width: 1024px) {
        margin-left: 6px;
      }
    }
  }

  .delete-button {
    padding: 6px;
    font-size: 25px;
    color: $lilac;
    cursor: pointer;

    &.enter {
      opacity: 0;
    }

    &.enter-active {
      transition: 0.3s;
      opacity: 1;
    }

    &.exit {
      opacity: 1;
    }

    &.exit-active {
      transition: 0.3s;
      opacity: 0;
    }
  }

  .update-button {
    padding: 6px;
    font-size: 25px;
    color: rgba($color: white, $alpha: 0.8);
    cursor: pointer;
    margin-left: auto;

    &.animation-active {
      svg {
        animation: rotateAnimation 500ms linear infinite;
        @keyframes rotateAnimation {
          0% {
            transform: rotate(0deg);
          }

          100% {
            transform: rotate(360deg);
          }
        }
      }
    }

    &.enter {
      opacity: 0;
    }

    &.enter-active {
      transition: 0.3s;
      opacity: 1;
    }

    &.exit {
      opacity: 1;
    }

    &.exit-active {
      transition: 0.3s;
      opacity: 0;
    }
  }
}
