
          @import "src/assets/styles/_vars.scss"; 
        
.jokes-widget {
  display: flex;
  flex-direction: column;
  width: calc(45% - 15px);
  padding: 15px;
  border-radius: 30px;
  position: relative;
  overflow: hidden;
  min-height: 200px;
  background-color: rgba($grey, 0.9);

  @media (max-width: 640px) {
    order: 2;
    padding: 10px;
    border-radius: 15px;
    width: calc(50% - 5px);
  }

  .inner-wrapper {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 15px;
    background-color: rgba($white, 0.1);
    border-radius: 30px;
    padding: 30px 15px;

    @media (max-width: 640px) {
      border-radius: 15px;
      padding: 10px;
      gap: 10px;
    }

    .widget-error {
      padding: 0;
      background: transparent;

      &.enter {
        opacity: 0;
      }

      &.enter-active {
        opacity: 1;
        transition: 0.5s;
      }
    }

    .joke-wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
      max-width: 100%;

      &.enter {
        opacity: 0;
      }

      &.enter-active {
        opacity: 1;
        transition: 0.5s;
      }

      h2 {
        color: $white;
        font-size: 30px;
        text-align: center;
        margin-bottom: 15px;

        @media (max-width: 1024px) {
          font-size: 28px;
        }

        @media (max-width: 640px) {
          font-size: 22px;
        }

        @media (max-width: 390px) {
          font-size: 18px;
          margin-bottom: 10px;
        }
      }

      .jokes-item {
        background-color: transparent;
        padding: 0;

        .text {
          p {
            @media (max-width: 640px) {
              padding: 10px;
              border-radius: 10px 10px 10px 0;
              font-size: 18px;
            }

            @media (max-width: 390px) {
              font-size: 16px;
            }
          }
        }
      }
    }

    a {
      color: $sky-blue;
      text-decoration: none;
      font-size: 20px;
      text-align: center;

      @media (max-width: 1024px) {
        font-size: 18px;
      }

      @media (max-width: 640px) {
        font-size: 16px;
      }

      @media (max-width: 390px) {
        font-size: 14px;
      }
    }
  }
}
